import lotusAddress from '../../components/picker-address/lotusAddress.vue'
import Popup from '../../components/propUp/index.vue'
import '../../common/popup.scss'
import { mapGetters } from 'vuex'
export default{
	data(){
		return{
			showNumber: '0', // 0表示隐藏弹窗
			nickname: '', // 现有的昵称
			USER:{
				userDis:'北京市',
				
			},
			lotusAddressData:{
				visible:false,
				provinceName:'',
				cityName:'',
				townName:'',
			},
		}
	},
	computed:{
		...mapGetters([
			'openID'
		])
	},
	watch:{
		openID(val){
			console.log(val)
		}
	},
	components:{
		lotusAddress,
		Popup
	},
	onLoad() {
		console.log(this.$store.state.openID)
	},
	methods:{
		// 昵称弹窗
		modifyNickname() {
			this.showNumber = '1'
		},
		
		//关闭弹窗
		calse() {
			this.showNumber = '0'
		},
		
		//确认
		success() {
			this.showNumber = '0';
			console.log(this.nickname)
		},
		
		uploadImg(){   //修改头像
			uni.chooseImage({
			    count: 1, 
			    sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album','camera'], //从相册选择
			    success: function (res) {
					console.log(res)
			        console.log(JSON.stringify(res.tempFilePaths));
			    },
				fail: function (err) {
					uni.showToast({
					    title: JSON.stringify(err),
						icon:'none',
					    duration: 2000
					});
				}
			});
		},
		changeDis() { //调用地址PICKER  //确认回调
			this.lotusAddressData.visible = true;
		},
		choseValue(res){   //确认回调
			console.log(res);
			this.lotusAddressData.visible = res.visible;
			this.lotusAddressData.provinceName = res.province;
			this.lotusAddressData.cityName = res.city;
			this.lotusAddressData.townName = res.town;
			this.USER.userDis = `${res.province} ${res.city} ${res.town}`; 
		}
	}
}
